@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.2.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,300);

.App {
  text-align: center;
}

.app {
  overflow: auto;
  font-family: Arial;
}

button {
  margin-right: 3%;
  background: none;
  border: none;
}

h1 {
  text-align: center;
  margin: 0 auto;
  padding: 2%;
  font-size: 480%;
  color: #000000e8;
  font-family: Roundhand, cursive;
  text-shadow: 1px 1px 0px #ededed, 4px 4px 0px rgba(0, 0, 0, 0.15);
  transition: transform 0.5s cubic-bezier(0.235, 0, 0.05, 0.95);
}
h1:hover {
  transform: perspective(3px) scale3d(1.05, 1.05, 1) translateZ(0) !important;
}

.search__input {
  width: 60%;
  height: 30px;
  font-family: OpenSans-Light;
  font-size: 100.666667%;
  color: #2b2828;
  padding-top: 1px;
  padding-right: 41px;
  padding-bottom: 1px;
  padding-left: 11px;
  filter: drop-shadow(7.071067811865475px 7.0710678118654755px 5px #000000);
  border-radius: 20px;
  filter: invert(100%);
  transition: transform 0.5s cubic-bezier(0.235, 0, 0.05, 0.95);
  margin-bottom: 2%;
}
.search__input:hover {
  transform: perspective(3px) scale3d(1.05, 1.05, 1) translateZ(0) !important;
}

.about {
  float: right;
  font: 70%/1.5 "Pacifico", Helvetica, sans-serif;
  color: #020202;
  padding-right: 2%;
  padding-top: 0.5%;
}

.contact {
  float: left;
  font: 10%/1.5 "Pacifico", Helvetica, sans-serif;
  padding-left: 1%;
  padding-top: 0.5%;
}
.header {
  padding-bottom: 3.5%;
  background-color: rgba(255, 66, 255, 0.11);
  border-radius: 50px;
  filter: drop-shadow(7.071067811865475px 7.0710678118654755px 5px #080808);
  margin-bottom: 1%;
}

#social-platforms {
  position: relative;
  font-size: 56%;
  margin-top: 0.5%;
}

/*Pen code from this point on*/
.btn {
  font-size: 7%;
  display: inline-block;
  border-radius: 20px;
  box-shadow: 0 1px 5px 0 rgba(14, 14, 14, 0.35);
  -webkit-transition: all 0.1s;
  -moz-transition: all 0.1s;
  transition: all 0.08s;
  overflow: hidden;
}

.btn:hover {
  box-shadow: 0 5px 15px 0 rgb(165, 255, 169);
}

.btn > span,
.btn-icon > i {
  float: left;
  padding: 12px;
  -webkit-transition: all 0.1s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
  line-height: 4px;
}

.btn > span {
  color: rgb(0, 0, 0);
}

.btn-icon > i {
  position: relative;
  width: 1px;
  text-align: center;
  color: rgb(255, 255, 255);
  background: #000000;
}

/*Facebook*/
.btn-facebook:hover > i,
.btn-facebook:focus > i {
  color: #3b5998;
}

.btn-facebook > span {
  background: #3b5998;
}

/*Twitter*/
.btn-twitter:hover > i,
.btn-twitter:focus > i {
  color: #55acee;
}

.btn-twitter > span {
  background: #55acee;
}

/*Google*/
.btn-googleplus:hover > i,
.btn-googleplus:focus > i {
  color: #dd4b39;
}

.btn-googleplus > span {
  background: #dd4b39;
}

/*Pinterest*/
.btn-pinterest:hover > i,
.btn-pinterest:focus > i {
  color: #cb2028;
}

.btn-pinterest > span {
  background: #cb2028;
}

/*LinkedIn*/
.btn-linkedin:hover > i,
.btn-linkedin:focus > i {
  color: #007bb6;
}

.btn-linkedin > span {
  background: #007bb6;
}

.UpBtn {
  border-color: transparent;
  background-color: rgb(90, 240, 153);
  transition: transform 0.5s cubic-bezier(0.235, 0, 0.05, 0.95);
  filter: drop-shadow(7.071067811865475px 7.0710678118654755px 5px #404040);
  font-family: Roundhand, cursive;
  border-radius: 50px;
  width: 7%;
  margin: 5%;
}

.UpBtn:hover {
  transform: perspective(1px) scale3d(1.1, 1.1, 1) translateZ(0) !important;
  cursor: pointer;
}
